.smc_card {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  color: #1c023b;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-top: 0.5vw;
  padding: 0.7rem;
  position: relative;
  align-items: center;
  transition: all ease-in 200ms 50ms;
}
.smc_card:hover,
.smc_card_active {
  background-color: var(--tab-bg-color);
}
.smc_card_contatiner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.smc_card_contatiner > img {
  width: 2rem;
  height: 2rem;
}

.smccc_inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
}
.smc_card_contatiner_inner > h6,
.smccc_inner > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.smc_card_contatiner_inner > p,
.smccc_inner > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
.menu_icon {
  height: 1.5rem;
  width: auto;
  cursor: pointer;
}
